/*
 * random.ts
 *     
 */
import { zeroStats } from "./stats";
import { chaosData } from "./data";
//       
export const isSucc = (succP) => (Math.random() < (succP / 100));
//         
const randomChaosOption = (probData) => {
    const rand = Math.random();
    let acc = 0;
    for (const i in probData) {
        if (rand >= acc && rand < acc + probData[i]) {
            return parseInt(i);
        }
        else {
            acc += probData[i];
        }
    }
};
//   ,        
export const createChaosStats = (chaosName, nowStats) => {
    const result = Object.assign({}, zeroStats);
    for (const i in nowStats) {
        if (nowStats[i] > 0) { //0   
            let enhancedValue = randomChaosOption(chaosData) * (i === " HP" || i === " MP" ? 10 : 1); // HP,  MP 10 
            switch (chaosName) {
                case "  ": // 
                    enhancedValue = Math.abs(enhancedValue);
                    break;
                case "  ": //5 6
                    switch (enhancedValue) {
                        case +5:
                            enhancedValue = +6;
                            break;
                        case -5:
                            enhancedValue = -6;
                            break;
                    }
                    break;
                case "   ": // ,  5 6
                    enhancedValue = Math.abs(enhancedValue);
                    enhancedValue = enhancedValue === 5 ? 6 : enhancedValue;
                    break;
            }
            if (nowStats[i] + enhancedValue < 0) { //0   
                enhancedValue = -nowStats[i];
            }
            result[i] = enhancedValue;
        }
    }
    return result;
};
