/*
 * itemStore.ts
 *     store
 */
import { writable, derived } from 'svelte/store';
import { createNumber, createStats } from "./storeUtils";
import { zeroStats, addStats } from "../stats";
export const isHammered = writable(false); //  
export const maxUpgrade = createNumber(3); // 
export const maxUpgradeWithHammer = derived(//   
[maxUpgrade, isHammered], ([$maxUpgrade, $isHammered]) => ($maxUpgrade + ($isHammered ? 1 : 0)));
export const succUpgradeCount = createNumber(0); // 
export const failUpgradeCount = createNumber(0); // 
export const canUpgradeCount = derived(// 
[maxUpgradeWithHammer, succUpgradeCount, failUpgradeCount], ([$maxUpgradeWithHammer, $succUpgradeCount, $failUpgradeCount]) => ($maxUpgradeWithHammer - ($succUpgradeCount + $failUpgradeCount)));
export const isReturned = writable(false); //  
export const isRecoveried = writable(false); //  
export const itemStats = createStats({
    "STR": 50,
    "DEX": 50,
    "INT": 50,
    "LUK": 50,
    "": 6,
    "": 6,
    "": 150,
    "": 0,
    "": 0,
    " HP": 150,
    " MP": 150
});
export const enhancedStats = createStats(Object.assign({}, zeroStats)); // 
export const totalStats = derived(//  
[itemStats, enhancedStats], ([$itemStats, $enhancedStats]) => addStats($itemStats, $enhancedStats));
