<!--
    ScrollSetter.svelte

      
    input number, input range  
-->

<script lang="ts">
    import ImageText from "./ImageText.svelte";

    export let imgSrc: string | null = null; // 
    export let text: string; //
    export let defaultValue: number; //
    export let min: number; //
    export let max: number = 99999; //; : 99999
    export let disableRange: boolean = false; //range  
    export let isPercent: boolean = false; //%

    export let value: number; //

    const checkValue = () => { //   
        if (value == null) { //  
            value = defaultValue;
        }
        if (value < min) { //  
            value = min;
        } 
        if (value > max) { //  
            value = max;
        }
    }
</script>

<main>
    {#if imgSrc !== null}
        <ImageText imgSrc={imgSrc} text={text + ": "} />
    {:else}
        <span>{text + ": "}</span>
    {/if}
    <span>
        <input type="number" bind:value={value} min={min} max={max} on:focusout={checkValue} />
        {#if isPercent}
            %
        {/if}
    </span>
    {#if !disableRange}
        <input type="range" bind:value={value} min={min} max={max} />
    {/if}
</main>

<style>
    main {
        margin: 2px;
    }
</style>
