import { createNumber } from "./storeUtils";
export const chaosCount = createNumber(0); // 
export const chaosOfGoodnessCount = createNumber(0); // 
export const incredibleChaosCount = createNumber(0); // 
export const incredibleChaosOfGoodnessCount = createNumber(0); // 
export const returnCount = createNumber(0); // 
export const recoveryCount = createNumber(0); // 
export const innocentCount = createNumber(0); // 
export const pureCount = createNumber(0); // 
export const hammerCount = createNumber(0); // 
export const resetAllCount = () => {
    chaosCount.reset();
    chaosOfGoodnessCount.reset();
    incredibleChaosCount.reset();
    incredibleChaosOfGoodnessCount.reset();
    returnCount.reset();
    recoveryCount.reset();
    innocentCount.reset();
    pureCount.reset();
    hammerCount.reset();
};
