<!--
    ItemUI.svelte

       UI 
-->

<script lang="ts">
    import { type Stats, zeroStats } from "../assets/stats";

    export let title: string = ""; // 
    export let totalStats: Stats = {...zeroStats}; // 
    export let itemStats: Stats = {...zeroStats}; // 
    export let enhancedStats: Stats = {...zeroStats}; // 
    export let succUpgradeCount: number = 0; // 
    export let failUpgradeCount: number = 0; //  
    export let canUpgradeCount: number = 0; // 
    export let isHammered: boolean = false; // 
    export let isReturned: boolean = false; // 
    export let isRecoveried: boolean = false; // 

    //     
    const getColorTotal = (totalStat: number, enhancedStat: number) => {
        if (totalStat === 0) { // 0 
            return "gray";
        } else if (enhancedStat > 0) { //   
            return "blue";
        } else if (enhancedStat < 0) { //   
            return "red";
        }
        return "";
    }

    //     
    const getColorEnhance = (enhancedStat: number) => {
        if (enhancedStat === 0) { //   
            return "gray";
        } else if (enhancedStat > 0) { //   
            return "blue";
        } else if (enhancedStat < 0) { //   
            return "red";
        }
        return "";
    }
</script>

<main>
    <h1>{title}</h1>
    <div class="maple-box">
        <!-- ; (+) -->
        <h2>{`(+${succUpgradeCount})`}</h2>
        <hr />
        <!--   -->
        <p class="stats">
            <span></span>
            <span></span>
            <span> </span>
        </p>
        <hr />
        <!--   -->
        {#each Object.entries(itemStats) as stat}
            <p class="stats">
                <!--  -->
                <span class={getColorTotal(totalStats[stat[0]], enhancedStats[stat[0]])}>
                    {stat[0]}
                </span>
                <!--   -->
                <span class={getColorTotal(totalStats[stat[0]], enhancedStats[stat[0]])}>
                    {totalStats[stat[0]] > 0 ? totalStats[stat[0]] : 0}
                </span>
                <!--   -->
                <span>
                    (
                    <span class="left-align">
                        {itemStats[stat[0]]}
                    </span>
                    <span class={"right-align " + getColorEnhance(enhancedStats[stat[0]])}>
                        {`${(enhancedStats[stat[0]] >= 0 ? "+" : "")}${enhancedStats[stat[0]]}`}
                    </span>
                    )
                </span>
            </p>
        {/each}
        <hr />
        <p>
            <!--  -->
            <span>  : {canUpgradeCount}</span>
            <!--    -->
            <span class="orange">(  : {failUpgradeCount})</span>
        </p>
        <!--    -->
        {#if isHammered}
            <p>  </p>
        {/if}
        {#if isReturned || isRecoveried}
            <hr />
            <!--   -->
            {#if isRecoveried}
                <p class="orange">    .</p>
            {/if}
            <!--   -->
            {#if isReturned}
                <p class="orange">    .</p>
            {/if}
        {/if}
    </div>
</main>

<style>
    main {
        height: 480px;
    }

    main div {
        width: 285px;
        margin: auto;
        padding: 0px;
        text-align: left;
        display: flex;
        flex-direction: column;
    }

    main h2 {
        text-align: center;
        margin: 10px auto auto;
    }

    hr {
        width: 100%;
    }

    main div p {
        margin: 0px 5px;
    }

    main div p:first-child {
        margin: 5px 5px 0px;
    }

    main div p:last-child {
        margin: 0px 5px 5px;
    }

    main div p:first-child span:nth-child(2),
    .stats>span:not(:last-child) {
        display: inline-block;
        width: 4.5em;
        text-align: left;
    }

    .left-align {
        display: inline-block;
        width: 2em;
        text-align: left;
    }

    .right-align {
        display: inline-block;
        width: 3em;
        text-align: right;
    }

    .orange {
        color: Orange
    }

    .gray {
        color: Gray;
    }

    .blue {
        color: LightSkyBlue;
    }

    .red {
        color: Crimson;
    }
</style>
