// Stats   
export const addStats = (a, b) => {
    const r = Object.assign({}, a);
    for (const i in r) {
        r[i] += b[i];
    }
    return r;
};
//  string   
export const updatedStatToString = (stat) => {
    const result = [];
    for (const i in stat) {
        if (stat[i] !== 0) { //0   
            result.push(`${i}: ${stat[i] > 0 ? "+" : ""}${stat[i]}`); //<>: <>
        }
    }
    return result.length !== 0
        ? result.join(", ") //<>: <>, <>: <>, ...
        : "   ."; //  
};
