/*
 * storeUtils.ts
 * store  ( ,  )  
 */
import { writable } from 'svelte/store';
import { addStats } from "../stats";
// initial , add, sub, reset, getDefault  Writable<number>  
export const createNumber = (initial) => {
    const { subscribe, set, update } = writable(initial);
    return {
        subscribe,
        set,
        update,
        add: (e) => {
            update(n => n + e);
        },
        sub: (e) => {
            update(n => n - e);
        },
        reset: () => {
            set(initial);
        },
        getDefault: () => initial
    };
};
// initial , add, reset, getDefault  Writable<Stats>  
export const createStats = (initial) => {
    const { subscribe, set, update } = writable(Object.assign({}, initial));
    return {
        subscribe,
        set,
        update,
        add: (e) => {
            update(n => addStats(n, e));
        },
        reset: () => {
            set(Object.assign({}, initial));
        },
        getDefault: () => (Object.assign({}, initial))
    };
};
// initial , add, reset, getDefault  Writable<string[]>  
export const createList = (initial) => {
    const { subscribe, set, update } = writable([...initial]);
    return {
        subscribe,
        set,
        update,
        add: (e) => {
            update(n => [...n, ...e]);
        },
        reset: () => {
            set([...initial]);
        },
        getDefault: () => ([...initial])
    };
};
