<!--
    UsingUI.svelte

      UI 
-->

<script lang="ts">
    import { get } from "svelte/store";
    import ImageText from "./ImageText.svelte";
    import ReturnUI from "./ReturnUI.svelte";
    import { totalStats, enhancedStats,
        succUpgradeCount, failUpgradeCount, canUpgradeCount,
        isHammered, isReturned, isRecoveried } from "../assets/store/itemStore";
    import { chaosP, chaosOfGoodnessP, incredibleChaosP, incredibleChaosOfGoodnessP, innocentP, pureP, hammerP } from "../assets/store/probStore";
    import { chaosCount, chaosOfGoodnessCount, incredibleChaosCount, incredibleChaosOfGoodnessCount,
        returnCount, recoveryCount, innocentCount, pureCount, hammerCount } from "../assets/store/countStore"
    import { enhanceHistory } from "../assets/store/historyStore";
    import type { NewWritable, WritableWithSub } from "../assets/store/storeUtils";
    import { type ChaosName, isSucc, createChaosStats } from "../assets/random";
    import { type Stats, zeroStats, addStats, updatedStatToString } from "../assets/stats";

    let showReturnUI = false; //ReturnUI   
    let updatedStatsForReturn: Stats = {...zeroStats}; //   ( )
    let chaosNameForReturn: ChaosName; //   ( )
    let succPForReturn: number; //   ()

    //ReturnUI  ; ReturnUI    
    const handleReturnMessage = (e: { detail: { use: boolean; chaosName: ChaosName; succP: number; }; }) => {
        showReturnUI = false; // ReturnUI 
        if (e.detail.use) { //  
            enhanceHistory.add([`   ${e.detail.chaosName} ${e.detail.succP}%    .`]);
        } else { //  
            succUpgradeCount.add(1);
            enhancedStats.update(n => addStats(n, updatedStatsForReturn));
            enhanceHistory.add([`   .`]);
        }
    }

    //      
    const makeUseChaosFunc = (succP: NewWritable<number>, chaosName: ChaosName, chaosCountWritable: NewWritable<number> & WritableWithSub<number>) => (() => {
        if (isSucc(get(succP))) {
            chaosCountWritable.add(1);
            const updatedStats = createChaosStats(chaosName, $totalStats);
            enhanceHistory.add([`${chaosName} ${get(succP)}%       .`]);
            enhanceHistory.add([`${chaosName} ${get(succP)}%  . (${updatedStatToString(updatedStats)})`]);

            if ($isReturned) { // 
                updatedStatsForReturn = updatedStats;
                chaosNameForReturn = chaosName;
                succPForReturn = get(succP);
                showReturnUI = true; //ReturnUI  handleReturnMessage  
            } else {
                succUpgradeCount.add(1);
                enhancedStats.update(n => addStats(n, updatedStats));
            }
        } else {
            if ($isRecoveried) {
                enhanceHistory.add([`   ${chaosName} ${get(succP)}%  .`]);
            } else {
                chaosCountWritable.add(1);
            }

            enhanceHistory.add([`${chaosName} ${get(succP)}%       .`]);

            if ($isReturned) {
                enhanceHistory.add(["   ."]);
            } else {
                failUpgradeCount.add(1);
            }

        }
        isRecoveried.set(false);
        isReturned.set(false);
    })

    const useChaos = makeUseChaosFunc(chaosP, " ", chaosCount); //  
    const useChaosOfGoodness = makeUseChaosFunc(chaosOfGoodnessP, "  ", chaosOfGoodnessCount); //  
    const useIncredibleChaos = makeUseChaosFunc(incredibleChaosP, "  ", incredibleChaosCount); //  
    const useIncredibleChaosOfGoodness = makeUseChaosFunc(incredibleChaosOfGoodnessP, "   ", incredibleChaosOfGoodnessCount); //  

    const useReturn = () => { //  
        returnCount.add(1);
        isReturned.set(true);
        enhanceHistory.add(["        ."]);
    };

    const useRecovery = () => { //  
        recoveryCount.add(1);
        isRecoveried.set(true);
        enhanceHistory.add(["        ."]);
    };

    const useInneocent: () => void = () => { //  
        innocentCount.add(1);
        if (isSucc($innocentP)) {
            enhancedStats.reset();
            succUpgradeCount.reset();
            failUpgradeCount.reset();
            isHammered.set(false);
            enhanceHistory.add([`  ${$innocentP}%       .`]);
        } else {
            enhanceHistory.add([`  ${$innocentP}%       .`]);
        }
    };

    const usePure = () => { //  
        pureCount.add(1);
        if (isSucc($pureP)) {
            failUpgradeCount.sub(1);
            enhanceHistory.add([`  ${$pureP}%       .`]);
        } else {
            enhanceHistory.add([`  ${$pureP}%       .`]);
        }
    };

    const useHammer: () => void = () => { //  
        hammerCount.add(1);
        isHammered.set(true);
        if (isSucc($hammerP)) {
            enhanceHistory.add(["   !"]);
        } else {
            failUpgradeCount.add(1);
            enhanceHistory.add(["   ."]);
        }
    };
</script>

<main>
    <h1></h1>
    <div>
        <button on:click={useChaos} disabled={$canUpgradeCount === 0}>
            <ImageText imgSrc="./images/Use_Chaos_Scroll.webp" text={`  ${$chaosP}% `} />
        </button>
        <button on:click={useChaosOfGoodness} disabled={$canUpgradeCount === 0}>
            <ImageText imgSrc="./images/Use_Chaos_Scroll.webp" text={`   ${$chaosOfGoodnessP}% `} />
        </button>
        <button on:click={useIncredibleChaos} disabled={$canUpgradeCount === 0}>
            <ImageText imgSrc="./images/Use_Chaos_Scroll.webp" text={`   ${$incredibleChaosP}% `} />
        </button>
        <button on:click={useIncredibleChaosOfGoodness} disabled={$canUpgradeCount === 0}>
            <ImageText imgSrc="./images/Use_Chaos_Scroll.webp" text={`    ${$incredibleChaosOfGoodnessP}% `} />
        </button>
        <button on:click={useReturn} disabled={$isReturned}>
            <ImageText imgSrc="./images/Return_Scroll.png" text="  " />
        </button>
        <button on:click={useRecovery} disabled={$isRecoveried}>
            <ImageText imgSrc="./images/Recovery_Shield.png" text="  " />
        </button>
        <button on:click={useInneocent} disabled={$succUpgradeCount === 0 && $failUpgradeCount === 0 && !$isHammered}>
            <ImageText imgSrc="./images/Use_Innocence_Scroll.webp" text={`  ${$innocentP}% `} />
        </button>
        <button on:click={usePure} disabled={$failUpgradeCount === 0}>
            <ImageText imgSrc="./images/Use_Clean_Slate_Scroll.webp" text={`  ${$pureP}% `} />
        </button>
        <button on:click={useHammer} disabled={$isHammered}>
            <ImageText imgSrc="./images/Use_Golden_Hammer.webp" text={`  ${$hammerP}% `} />
        </button>
    </div>
    {#if showReturnUI}
        <ReturnUI
            updatedStats={updatedStatsForReturn}
            chaosName={chaosNameForReturn}
            succP={succPForReturn}
            on:message={handleReturnMessage}
        />
    {/if}
</main>

<style>
    main div {
        display: flex;
        flex-direction: column;
    }

    main div button {
        font-size: 19px;
        margin: 3px;
        width: 17em;
    }

</style>
